.background {
	position: absolute;
	left: 0;
	width: calc(100% - 300px);
	height: 100%;
	background: url(../assets/images/bg-login.jpg);
	background-size: cover;
}

section#conteudo-view {
	position: absolute;
	right: 0;
	float: right;
	width: 300px;
	height: 100%;
	background: #51dca6;
	padding: 20px;
}

section#conteudo-view h1 {
	font: 3em 'Fredoka One', cursive;
	color: #f3f3f3;
	float: left;
	width: 100%;
	margin-top: 50px;
}

section#conteudo-view form {
	margin-top: 40px;
}

section#conteudo-view h3,
section#conteudo-view form p {
	font: 1em 'Fredoka One', cursive;
	color: #f3f3f3;
	float: left;
	width: 100%;
}

section#conteudo-view form,
section#conteudo-view form label {
	float: left;
	width: 100%;
	margin-bottom: 10px;
}

section#conteudo-view form p {
	font-size: 1.25em;
}

section#conteudo-view form input[type='submit'],
section#conteudo-view form label input {
	float: left;
	width: 100%;
	height: 40px;
	border-radius: 20px;
	padding: 0 20px;
	border: 2px solid #52ca9b;
	color: #959595;
	font: 1em 'Arial';
}

section#conteudo-view form input[type='submit'] {
	background: #00ff9c;
	text-align: center;
	font: 1em 'Fredoka One', cursive;
	width: 60%;
	float: right;
	color: #959595;
}
